.numpad-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.numpad-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;
  height: 70%;
}

.numpad-btn {
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1.5rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.numpad-btn:active:not(:disabled) {
  background: #f0f0f0;
}

.clear-btn {
  background: #ffebee;
  color: #c62828;
}

.action-buttons {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
  height: 25%;
}

.action-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  border-radius: 8px;
}

.detect-btn {
  background: #fff8e1;
  color: #ff8f00;
}

.receipt-btn {
  background: #e3f2fd;
  color: #1565c0;
}

.btn-icon {
  width: 24px;
  height: 24px;
  margin-bottom: 5px;
}
